import styled from 'styled-components';
import { Modal } from 'react-bootstrap';
import { H2, P } from '../common/Typography';
import parse from 'html-react-parser';
import { Fragment } from 'react';

const StyledModal = styled(Modal)`
  padding: 0 !important;
  .modal-dialog {
    max-width: 985px;
    padding: 0 20px;
  }

  .connect-success-content {
    background: #fff;
    border-radius: 10px;
    width: 100%;
    overflow: hidden;
    border: none;
  }

  .modal-header {
    height: 112px;
    background: #e2ae39;
    padding: 21px;
    @media screen and (max-width: 991px) {
      height: 70px;
    }
    @media screen and (max-width: 550px) {
      height: 50px;
      padding: 13px;
    }
  }

  .modal-text {
    color: #000;
    margin-bottom: 32px;
  }

  .modal-title {
    line-height: 1.2;
    text-transform: uppercase;
    @media screen and (max-width: 768px) {
      font-size: 40px;
    }
    @media screen and (max-width: 550px) {
      font-size: 24px;
    }
  }

  .font-italic {
    font-style: italic;
  }

  .close-btn {
    margin: 0;
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.14);
    color: #ffffff;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    padding: 0;
    border: 0;
    img {
      width: 100%;
    }
    @media screen and (max-width: 991px) {
      width: 49px;
      height: 49px;
    }
    @media screen and (max-width: 550px) {
      width: 37px;
      height: 37px;
    }
  }
`;

const ContentWrapper = styled.div`
  background: white;
  display: flex;
  align-items: center;
  flex-flow: column;
  padding: 48px 46px 48px 46px;
  p {
    line-height: 1.2;
  }
  @media screen and (max-width: 991px) {
    padding: 48px;
  }
  @media screen and (max-width: 550px) {
    padding: 20px;
    p {
      font-size: 12px;
    }
  }
`;
const TextWrapper = styled.div`
  display: flex;
  align-items: center;
  flex-flow: column;
  max-width: 750px;
  margin: auto;
  letter-spacing: 0;
  .add-images {
    margin-top: 90px;
    margin-bottom: 62px;
    width: 100%;
  }
  img {
    /* height: 419px; */
    border-radius: 25px;
  }
  .mdc-club-image {
    width: 100%;
  }
  .md-recers-image {
    img {
      width: 386px;
    }
  }
  .mdc-collection-image {
    img {
      border-radius: 10px;
    }
  }
  @media screen and (max-width: 991px) {
    .mdc-club-image {
      img {
        width: 45%;
      }
    }
    .add-images {
      .add-image {
        width: 25%;
        border-radius: 25px;
      }
      .plus-icon {
        width: 40px;
      }
    }
  }
  @media screen and (max-width: 768px) {
    .add-images {
      margin: 0px;
      .plus-icon {
        width: 25px;
      }
    }
    img {
      width: 350px;
    }
    .md-recers-image {
      img {
        width: 350px;
      }
    }
  }
  @media screen and (max-width: 425px) {
    img {
      width: 200px;
    }
    .md-recers-image {
      img {
        width: 200px;
      }
    }
  }
  @media screen and (max-width: 320px) {
    .img-cover {
      width: 90%;
      img {
        width: 100%;
      }
    }
  }
`;

interface LearnMoreModalProps {
  show: boolean;
  onClose: () => void;
  data: {
    title: string;
    mainText?: string[];
    listText?: {
      title: string;
      text: string[];
    }[];
    addImages?: string[];
    images?: string[];
  };
}

const LearnMoreModal = ({ show, onClose, data }: LearnMoreModalProps) => {
  return (
    <StyledModal show={show} onHide={onClose} centered contentClassName="connect-success-content">
      <Modal.Header>
        <button className="close-btn" onClick={onClose}>
          <img src="/assets/images/closeBtn.svg" alt="close-btn" />
        </button>
      </Modal.Header>
      <Modal.Body>
        <ContentWrapper>
          <H2 className="modal-text modal-title text-center" fontWeight={700}>
            {parse(data.title)}
          </H2>
          <TextWrapper>
            {data.mainText?.map((text, index) => (
              <P
                key={index}
                className={'modal-text text-center ' + (index === 0 ? 'font-italic' : '')}
                fontSize={'24px'}
              >
                {parse(text)}
              </P>
            ))}
            {data.listText?.map((list, index) => (
              <Fragment key={index}>
                <P fontWeight={700} fontSize="24px" className="modal-text text-center mb-0">
                  {parse(list.title)}
                </P>
                {list.text?.map((item, i) => (
                  <P
                    fontSize="24px"
                    className={
                      'modal-text text-center ' + (i + 1 === list.text.length ? '' : 'mb-0')
                    }
                    key={i}
                  >
                    {item}
                  </P>
                ))}
              </Fragment>
            ))}
            {data.addImages ? (
              <div className="add-images d-flex align-items-center justify-content-between">
                {data.addImages.map((image, index) => (
                  <Fragment key={index}>
                    {index != 0 ? (
                      <img
                        src="/assets/images/plus_icon.svg"
                        className="plus-icon"
                        alt="plus-icon"
                      />
                    ) : (
                      ''
                    )}
                    <img src={image} className="add-image" alt={'add-image-' + index} />
                  </Fragment>
                ))}
              </div>
            ) : (
              ''
            )}
            <div
              className={
                'img-cover d-flex mt-5 ' +
                (data.title === 'MAD DOG<br/>CAR COLLECTION'
                  ? 'mdc-collection-image justify-content-center'
                  : data.title === 'MD License'
                  ? 'mdc-license-image justify-content-center'
                  : data.title === 'MD RACERS'
                  ? 'md-recers-image'
                  : 'justify-content-center')
              }
            >
              {data.images?.map((image, index) => (
                <img src={image} key={index} alt={data.title} />
              ))}
            </div>
          </TextWrapper>
        </ContentWrapper>
      </Modal.Body>
    </StyledModal>
  );
};

export default LearnMoreModal;
