const AccumulatePage = () => {
  const isMobile = useMedia('(max-width: 768px)');
  const { lucid, accumulating, myWalletAddress } = useWalletConnect();
  const [showAccumlating, setShowAccumulating] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [showLearnMoreModal, setShowLearnMoreModal] = useState(false);
  const [currentPartnerId, setCurrentPartnerId] = useState(0);
  const [isCardData, setIsCardData] = useState(false);

  const handleClick = async () => {
    if (myWalletAddress) {
      const hasNFTsPartners = await axios.get(
        `https://jn6eig5lc8.execute-api.eu-west-2.amazonaws.com/default/hasNFTsPartners?address=${myWalletAddress}`
      );
      if (accumulating) {
        setAlertMessage('You are already accumulating!');
        setShowAccumulating(true);
      }
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      else if (!hasNFTsPartners.data.rewards) {
        setAlertMessage('You do not have any assets that are applicable');
        setShowAccumulating(true);
      } else if (lucid) {
        try {
          const tx = await lucid
            .newTx()
            .payToAddress('addr1vxcv899n9w76ca6s83kfkxw2yadeha7rqh3vwzzyu5004lqng6snf', {
              lovelace: 5000000n
            })
            .complete();
          const signedTx = await tx.sign().complete();

          const txHash = await signedTx.submit();

          setAlertMessage('Transaction was successful');
          setShowAccumulating(true);

          console.log(txHash);
        } catch (err) {
          setAlertMessage('Transaction was unsuccessful');
          setShowAccumulating(true);
        }
      }
    } else {
      setAlertMessage('Please connect your wallet first!');
      setShowAccumulating(true);
    }
  };

  return (
    <Wrapper className="page">
      <Container>
        <H2 fontWeight={700} textAlign="center">
          ACCUMULATE REWARDS <br />
          FROM YOUR ASSETS
        </H2>
        <SubtitleWrapper textAlign="center">
          <br />
          When you accumulate from your assets, you will be able to collect rewards. You will{' '}
          <span className="text-yellow">NOT </span>
          be able to collect your rewards if you have not clicked
          <br />“<span className="text-yellow">Start Accumulating Rewards</span>”.
          <br />
          <span className="font-italic">Click each project to learn more.</span>
        </SubtitleWrapper>
        <AssetsList className="assets-list-btn" marginBottom="150px">
          <Button
            onClick={handleClick}
            className="btn-top"
            width="auto"
            padding="20px 40px"
            fontSize="32px"
          >
            ACCUMULATE REWARDS
          </Button>
        </AssetsList>
        <AssetsList className="first-assets-list">
          {cardData.map((item, index) => (
            <div key={index} className="partner-card d-flex flex-column align-item-center">
              <AssetCard
                src={item.image}
                isVideoCard={item.isVideo}
                title={item.title}
                width={'347px'}
              />
              <Button
                width="auto"
                padding="20px 58px"
                fontSize="32px"
                className="btn-card"
                onClick={() => {
                  setShowLearnMoreModal(true);
                  setCurrentPartnerId(index);
                  setIsCardData(true);
                }}
              >
                LEARN MORE
              </Button>
            </div>
          ))}
        </AssetsList>
        <H2 textAlign="center" fontWeight={700} className="mb-4">
          PARTNERS
        </H2>
        <SubtitleWrapper textAlign="center" className="text-click font-italic">
          Click each project to learn more.
        </SubtitleWrapper>
        <br />
        <br />
        <AssetsList maxWidth={'900px'} marginBottom={'50px'}>
          {partnersData.map((partner, index) => (
            <div key={index} className="partner-card d-flex flex-column align-item-center">
              <AssetCard src={partner.image} title={partner.title} width={'347px'} />
              <Button
                width="auto"
                className="btn-card"
                padding="20px 58px"
                fontSize="32px"
                onClick={() => {
                  setShowLearnMoreModal(true);
                  setCurrentPartnerId(index);
                  setIsCardData(false);
                }}
              >
                LEARN MORE
              </Button>
            </div>
          ))}
        </AssetsList>
      </Container>
      <AlertModal
        show={showAccumlating}
        onClose={() => setShowAccumulating(false)}
        message={alertMessage}
      />
      <LearnMoreModal
        show={showLearnMoreModal}
        onClose={() => setShowLearnMoreModal(false)}
        data={
          isCardData
            ? cardData[currentPartnerId].modalText
            : partnersData[currentPartnerId].modalText
        }
      />
    </Wrapper>
  );
};

export default AccumulatePage;
